<template>
  <div class="user-main">
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <el-button icon="el-icon-plus" type="warning" @click="openModal()" size="small">{{$t('common.add')}}</el-button>
    </div>
    <el-form :model="userForm" ref="userForm" label-width="30px" label-position="right" size="small">
      <el-row class="label-first" :gutter="20">
        <el-col :lg="6" :xl="4">
          <el-form-item label="IP：">
            <el-input v-model.trim="userForm.hostIp"></el-input>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-button type="primary" icon="el-icon-search" @click="searchUserInfo" size="small">{{$t('common.search')}}</el-button>
        </el-col>
      </el-row>
    </el-form>
    <el-table :data="userInfo.list" stripe>
      <el-table-column prop="hostIp" label="主机IP" align="center"></el-table-column>
      <el-table-column prop="username" label="用户名" align="center"></el-table-column>
      <el-table-column label="密码" align="center">
        <template slot-scope="props">
          {{props.row.password | pwdFormat}}
        </template>
      </el-table-column>
      <el-table-column :label="$t('common.operation')" align="center" fixed="right" width="140">
        <template slot-scope="props">
          <el-button type="text" size="mini" icon="el-icon-edit" @click="openModal(props.row)">{{$t('common.modify')}}
          </el-button>
          <el-button type="text" class="btn-text-red" size="mini" icon="el-icon-delete" @click="deleteItem(props.row)">{{ $t('common.delete')}}
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination background @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="pageSize" layout="total, prev, pager, next, jumper" :total="userInfo.total"></el-pagination>
    <!-- 添加/修改 -->
    <el-dialog
      :title="userTitle"
      :visible.sync="userVisible"
      width="30%"
      center
      @close="cancel">
      <el-form :model="addForm" ref="addForm" :rules="addFormRules" label-width="80px">
        <el-form-item label="主机IP" prop="hostIp">
          <gl-ip-input v-model="addForm.hostIp" @getIpValue="getIpValue"></gl-ip-input>
        </el-form-item>
        <el-form-item label="用户名" prop="username">
          <el-input v-model.trim="addForm.username" maxlength="30"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input type="password" v-model.trim="addForm.password" maxlength="30"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="cancel" size="small">{{ $t('common.cancel') }}</el-button>
        <el-button type="primary" @click="onSubmit" size="small">{{ $t('common.ok') }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex'

  export default {
    data() {
      return {
        currentPage: 1,
        pageSize: 10,
        userForm: {
          hostIp: ''
        },
        userVisible: false,
        userTitle: '',
        addForm: {},
        addFormRules: {
          username: [
            { required: true, message: '请输入用户名', trigger: 'blur' }
          ],
          password: [
            { required: true, message: '请输入密码', trigger: 'blur' }
          ]
        },
        oldParams: {}
      }
    },
    computed: {
      ...mapState({
        userInfo: state => state.host.userInfo
      })
    },
    created() {
      this.getTableData()
    },
    methods: {
      ...mapActions('host', ['getUserList', 'addHostUser', 'updateHostUser', 'delteHostUser']),
      getTableData() {
        const vm = this
        const params = {
          pageNo: vm.currentPage,
          limit: vm.pageSize,
          baseParam: vm.userForm
        }
        vm.getUserList(params)
      },
      searchUserInfo() {
        this.getTableData()
      },
      handleCurrentChange(val) {
        this.currentPage = val
        this.getTableData()
      },
      getIpValue(data) {
        const vm = this
        vm.addForm.hostIp = data
      },
      openModal(props) {
        if (props && props.id) {
          this.userTitle = '修改主机用户'
          this.addForm = {...props}
          this.oldParams = {
            oldHostIp: props.hostIp,
            oldUsername: props.username
          }
        } else {
          this.userTitle = '添加主机用户'
          this.addForm = {}
        }
        this.userVisible = true
      },
      deleteItem(props) {
        const vm = this
        vm.$confirm(`是否删除${props.hostIp}？`, '删除', {
          confirmButtonText: vm.$t('common.ok'),
          cancelButtonText: vm.$t('common.cancel'),
          type: 'warning',
          center: true
        }).then(async () => {
          try {
            await vm.delteHostUser({id: props.id})
            vm.$message({
              type: 'success',
              message: '删除成功！'
            })
            vm.getTableData()
          } catch (error) {
            vm.$message({
              type: 'success',
              message: (error.data && error.data.message) || '删除失败'
            })
          }
        }).catch(() => {
        })
      },
      cancel() {
        this.$refs['addForm'].clearValidate()
        this.userVisible = false
      },
      onSubmit() {
        const vm = this
        vm.$refs['addForm'].validate(async valid => {
          if (valid) {
            if (!vm.addForm.hostIp || (vm.addForm.hostIp && vm.addForm.hostIp.length <= 3)) {
              vm.$message({
                type: 'warning',
                message: '请输入IP'
              })
              return
            }
            if (!vm.commonFunc.validateIp(vm.addForm.hostIp)) {
              vm.$message({
                type: 'warning',
                message: 'IP格式不正确'
              })
              return
            }
            if (vm.addForm.id) {
              try {
                await vm.updateHostUser([vm.oldParams, vm.addForm])
                vm.$message({
                  type: 'success',
                  message: '修改成功'
                })
                vm.cancel()
                vm.getTableData()
              } catch (error) {
                vm.$message({
                  type: 'error',
                  message: (error.data && error.data.message) || '修改失败'
                })
              }
            } else {
              try {
                await vm.addHostUser(vm.addForm)
                vm.$message({
                  type: 'success',
                  message: '添加成功'
                })
                vm.cancel()
                vm.getTableData()
              } catch (error) {
                vm.$message({
                  type: 'error',
                  message: (error.data && error.data.message) || '添加失败'
                })
              }
            }
          }
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../assets/common";
</style>



